import { getQueryRoute, platformCategory } from 'api/queries/query';
import { breakdownDescKey, breakdownTitleKey, orgUnitIdKey } from 'api/queries/query';
import { parseQuery } from 'api/queries/query';
export const getBreakdownPath = ({ basePath, description, groupBy, isPlatformCosts, label, }) => {
    const queryFromRoute = parseQuery(location.search);
    const newQuery = Object.assign(Object.assign(Object.assign(Object.assign(Object.assign({}, queryFromRoute), (description && description !== label && { [breakdownDescKey]: description })), (isPlatformCosts && { [breakdownTitleKey]: label })), { group_by: {
            [groupBy]: label,
        } }), (isPlatformCosts && {
        group_by: {
            [groupBy]: ['kube-', 'openshift-'],
        },
    }));
    return `${basePath}?${getQueryRoute(newQuery)}`;
};
export const getOrgBreakdownPath = ({ basePath, description, groupBy, groupByOrg, id, title, type, }) => {
    const queryFromRoute = parseQuery(location.search);
    const newQuery = Object.assign(Object.assign(Object.assign(Object.assign(Object.assign({}, queryFromRoute), (description && description !== title && { [breakdownDescKey]: description })), (title && { [breakdownTitleKey]: title })), (groupByOrg && { [orgUnitIdKey]: groupByOrg })), { group_by: {
            [groupBy]: id, // This may be overridden below
        } });
    if (type === 'account') {
        if (!newQuery.filter) {
            newQuery.filter = {};
        }
        newQuery.filter.account = id;
        newQuery.group_by = {
            [orgUnitIdKey]: groupByOrg,
        };
    }
    else if (type === 'organizational_unit') {
        newQuery.group_by = {
            [orgUnitIdKey]: id,
        };
    }
    return `${basePath}?${getQueryRoute(newQuery)}`;
};
export const isPlatformCosts = (queryFromRoute) => {
    let result = false;
    // Note that "kube-" and "openshift-" are provided only when users select the 'platform' project.
    // The category below is mainly used to restore state for the previous page
    if (Array.isArray(queryFromRoute.group_by.project)) {
        result =
            queryFromRoute.group_by.project.includes('kube-') && queryFromRoute.group_by.project.includes('openshift-');
    }
    return result && queryFromRoute.category === platformCategory;
};
